import React, { Component } from 'react';
import Form from 'react-bootstrap/lib/Form';
import InputGroup from 'react-bootstrap/lib/InputGroup';
import Grid from 'react-bootstrap/lib/Grid';
import InputGroupAddon from 'react-bootstrap/lib/InputGroupAddon';
import Panel from 'react-bootstrap/lib/Panel';
import Row from 'react-bootstrap/lib/Row';
import Button from 'react-bootstrap/lib/Button';
import CheckBox from 'react-bootstrap/lib/Checkbox';
import SignUpController from './controller';
import './style.css'


export default class SignUpScreen extends Component {
	constructor(props) {
		super(props);

		this.init();
	}

	init() {
		let initState = {
			user_name: '',
			password: '',
			first_name: '',
			last_name: '',
			mobile_number: '',
			phone_number: '',
			businnes_name: '',
			country: 'IND',
			address: '',
			city: '',
			state: '',
			zip_code: '',
			isSignUpSuccesful: false,
			doesCustomerWithSameEmailExists: false,
			wrongUsernamePasswordMessage: 'Sorry. Email Already Exists.',
			isEnterUsernameError: false,
			enterUsernameMessage: 'Please enter valid user name.',
			isEnterPasswordError: false,
			enterPasswordError: 'Please enter password.',
		};

		this.state = initState;
	}
	signUpSuccesful() {
		// let state = this.state;
		// state.isSignUpSuccesful = true;
		// this.setState(state);
		alert("SignUp Successful! Welcome Aboard.")
	}
	handleSubmitPressed = async () => {
		this.signUpSuccesful();
		let UserCredentials = {
			user_name: this.state.user_name,
			password: this.state.password,
			first_name: this.state.first_name,
			last_name: this.state.last_name,
			mobile_number: this.state.mobile_number,
			phone_no: this.state.phone_number,
			company: {
				name: this.state.businnes_name,
				country: this.state.country,
				state: this.state.state,
				city: this.state.city,
				address: this.state.address,
				zip_code: this.state.zip_code
			}
		};
		let Responce = await SignUpController.signUp(UserCredentials);
		if (Responce.status) {
			
		}
		else
			alert(Responce);
	}

	handleUserNameChange(text) { let state = this.state; state.user_name = text; this.setState(state); }
	handlePasswordChange(text) { let state = this.state; state.password = text; this.setState(state); }
	handleFirstNameChange(text) { let state = this.state; state.first_name = text; this.setState(state); }
	handleLastNameChange(text) { let state = this.state; state.last_name = text; this.setState(state); }
	handleBusinessNameChange(text) { let state = this.state; state.businnes_name = text; this.setState(state); }
	handleMobileNumberChange(text) { let state = this.state; state.mobile_number = text; this.setState(state); }
	handlePhoneNumberChange(text) { let state = this.state; state.phone_number = text; this.setState(state); }
	handleAddressChange(text) { let state = this.state; state.address = text; this.setState(state); }
	handleCityChange(text) { let state = this.state; state.city = text; this.setState(state); }
	handleStateChange(text) { let state = this.state; state.state = text; this.setState(state); }
	handleZipCodeChange(text) { let state = this.state; state.zip_code = text; this.setState(state); }




	render() {
		if (!this.state.isSignUpSuccesful)
			return (
				<div className="bg">
					<Panel className="signUpPanel " >
						<h3 id="lblPanel">Travel Partner SignUp</h3>
						<div className="line"></div>
						<span class="numberCircle">1</span>
						<span><h3 className="txtColor txtStep"> Your Basic Information</h3></span>
						<Form className="form-inline step">
							<Grid className="grdUnv">
								<Row className="justify-content-start">
									<InputGroup className="col-md-4 grdLeftColoumn ">
										<input type="text" className="form-control borderRadius" id="txtFirstName" placeholder="First Name" aria-describedby="basic-addon1"
											onChange={(event) => this.handleFirstNameChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-4 grdCenterColoumn ">
										<input type="text" className="form-control borderRadius" id="txtLastName" placeholder="Last Name" aria-describedby="basic-addon1"
											onChange={(event) => this.handleLastNameChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-4 grdRightColoumn">
										<InputGroupAddon className="inputGRP glyphicon glyphicon-phone" > </InputGroupAddon>
										<input type="text" className="form-control" id="txtMobilePhone" placeholder="Mobile No" aria-describedby="basic-addon1"
											onChange={(event) => this.handleMobileNumberChange(event.target.value)}></input>
									</InputGroup>
								</Row>
								<Row className=" justify-content-start step">
									<InputGroup className="col-md-6 grdLeftColoumn">
										<InputGroupAddon className="inputGRP glyphicon glyphicon-user" > </InputGroupAddon>
										<input type="text" className="form-control " id="txtEmail" placeholder="Your Email" aria-describedby="basic-addon1"
											onChange={(event) => this.handleUserNameChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-6 grdCenterColoumn">
										<InputGroupAddon className="inputGRP  glyphicon glyphicon-option-horizontal" > </InputGroupAddon>
										<input type="password" className="form-control" name="password"
											id="txtPassword" placeholder="Choose a Password" aria-describedby="basic-addon1"
											onChange={(event) => this.handlePasswordChange(event.target.value)}></input>
									</InputGroup>
								</Row>
							</Grid>

							<div className="line lineStep"></div>
							<span class="numberCircle">2</span>
							<span><h3 className="txtColor txtStep"> Your Business Information</h3></span>
							<Grid className="grdUnv">
								<Row className="justify-content-start step">
									<InputGroup className="col-md-8 grdLeftColoumn">
										<InputGroupAddon className="inputGRP glyphicon glyphicon-copyright-mark" > </InputGroupAddon>
										<input type="text" className="form-control " id="txtBusiness" placeholder="Business Name" aria-describedby="basic-addon1"
											onChange={(event) => this.handleBusinessNameChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-4 grdCenterColoumn">
										<InputGroupAddon className="inputGRP glyphicon glyphicon-phone-alt " > </InputGroupAddon>
										<input type="text" className="form-control" id="txtLandLine" placeholder="Phone" aria-describedby="basic-addon1"
											onChange={(event) => this.handlePhoneNumberChange(event.target.value)}></input>
									</InputGroup>
								</Row>
								<Row className='justify-content-start step'>
									<InputGroup className="col-md-6 grdLeftColoumn">
										<input type="text" className="form-control borderRadius" id="txtStreetAddress" placeholder="Street Address" aria-describedby="basic-addon1"
											onChange={(event) => this.handleAddressChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-2 grdCenterColoumn">
										<input type="text" className="form-control borderRadius" id="txtCity" placeholder="City" aria-describedby="basic-addon1"
											onChange={(event) => this.handleCityChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-2 grdRightColoumn">
										<input type="text" className="form-control borderRadius" id="txtState" placeholder="State" aria-describedby="basic-addon1"
											onChange={(event) => this.handleStateChange(event.target.value)}></input>
									</InputGroup>
									<InputGroup className="col-md-2 grdRightColoumn">
										<input type="text" className="form-control borderRadius" id="txtZip" placeholder="Zip Code" aria-describedby="basic-addon1"
											onChange={(event) => this.handleZipCodeChange(event.target.value)}></input>
									</InputGroup>
								</Row>
							</Grid>
						</Form>
						<Form className="form-inline step ipt inputGRP">
							<CheckBox id="chkbxTaC"><a>I Agree with terms and conditions</a></CheckBox>
							<a id="lnkTaC"><href>Read Terms and conditions</href></a>
							<Button className="form-control ipt" id="btnSignUp"
								onClick={() => this.handleSubmitPressed()}>Join Barak-Allah Journey</Button>
						</Form>
					</Panel>
				</div>
			);
		else
			return (<h1>Sign Up Succesful</h1>);

	}
}






