const rootUrls = require('../globalApiResources/apiResources');

module.exports = {
    rootUrlWithoutSession : `${rootUrls.devHost}:${rootUrls.devPort}/${rootUrls.apiPrefix}`,
    rootUrlWithSession : `${rootUrls.devHost}:${rootUrls.devPort}/${rootUrls.apiPrefix}/${rootUrls.sessionPrefix}`,

    signUp : {
        partnerPrefix : 'partner',
        signUpUrl : 'user',
        signUpMethod : "Post",
        headers : rootUrls.headers
    }
}


