const Request = require('../../helper/requestHelper'),
      partnerUrls = require('./res');
export default {
    signUp : async function (UserCredentials) {
        UserCredentials.headers = partnerUrls.signUp.headers;
        return await Request.default.requestWithBody
        (
           `${partnerUrls.rootUrlWithoutSession}/${partnerUrls.signUp.partnerPrefix}/${partnerUrls.signUp.signUpUrl}`,
            partnerUrls.signUp.signUpMethod,
            UserCredentials
        );
    }
}


